import React, { Component } from 'react';
import Web3 from 'web3'
import './App.css';
import Color from '../abis/Color.json'
//import ColorHelper from '../abis/ColorHelper.json'
import Tilt from 'react-parallax-tilt'
import mergeImages from 'merge-images';
import b64 from 'base64-js';
import swal from 'sweetalert';
import ProgressBar from "@ramonak/react-progress-bar";
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import { CircularProgressbarWithChildren } from 'react-circular-progressbar';
import 'react-pro-sidebar/dist/css/styles.css';
import { ProSidebar, SidebarHeader, SidebarFooter, SidebarContent, Menu, MenuItem, SubMenu} from 'react-pro-sidebar';
import Particles from 'react-particles-js';
//import * as VanillaTilt from './vanilla-tilt.js'
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
// import App2 from "./App2"; 
import { Link,HashRouter , Switch, Route } from 'react-router-dom';
import html2canvas from 'html2canvas';
import * as htmlToImage from "html-to-image";
import Footer from './Footer';

class Page1 extends Component {
  
  async componentWillMount() {
    await this.loadWeb3()
    await this.loadBlockchainData()
  }

  async loadWeb3() {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum)
      await window.ethereum.enable()
    }
    else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
    }
    else {
      window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
    }
  }

  async loadBlockchainData() {
    const web3 = window.web3

    const accounts = await web3.eth.getAccounts()
    this.setState({ account: accounts[0] })

    const networkId = await web3.eth.net.getId()
    const networkData = Color.networks[networkId]

    if(networkData) {
      const abi = Color.abi
      const address = networkData.address
      const contract = new web3.eth.Contract(abi, address)


      this.setState({ contract })
      this.state.adreskontraktu = address;
      const totalSupply = await contract.methods.totalSupply().call()
      this.setState({ totalSupply })

      
      const recently = []
      if(totalSupply>=6){
        this.setState({
          recently:[...this.state.recently, this.state.totalSupply-1,this.state.totalSupply-2, this.state.totalSupply-3,this.state.totalSupply-4,this.state.totalSupply-5,this.state.totalSupply-6]
        })

        for (var i = 0; i <= 5; i++) {
          const item3 = await contract.methods.items(this.state.recently[i]).call()
          console.log(this.state.recently[i]);
          const item_attack3 = item3.Attack;
          const item_Defense3 = item3.Defense;
          const item_Intelligence3 = item3.Intelligence;
          const item_klasabroni3 = item3.klasabroni;
          const item_level3 = item3.level;
          const item_rarity3 = item3.rarity;
          const item_w13 = item3.w_k_b1;
          const item_w23 = item3.w_k_b2;
          const item_w33 = item3.w_k_b3;
          const item_w43 = item3.w_k_b4;
          const item_w53 = item3.w_k_b5;
          const item_w63 = item3.w_k_b6;
          const item_w73 = item3.w_k_b7;
          const item_w83 = item3.w_k_b8;
          const id3=this.state.totalSupply-1-i;

          const zbiorcze = [item_attack3,item_Defense3,item_Intelligence3,item_klasabroni3,item_level3,item_rarity3];
          this.setState({
            itemsx3: [...this.state.itemsx3, [zbiorcze, item_rarity3, item_w13, item_w23, item_w33, item_w43, item_w53, item_w63, item_w73, item_w83,id3]]
          })
        }
      }



      for (var i = 1; i <= totalSupply; i++) {
        const item2 = await contract.methods.items(i - 1).call()

        const item_attack = item2.Attack;
        const item_Defense = item2.Defense;
        const item_Intelligence = item2.Intelligence;
        const item_klasabroni = item2.klasabroni;
        const item_level = item2.level;
        const item_rarity = item2.rarity;
        const item_w1 = item2.w_k_b1;
        const item_w2 = item2.w_k_b2;
        const item_w3 = item2.w_k_b3;
        const item_w4 = item2.w_k_b4;
        const item_w5 = item2.w_k_b5;
        const item_w6 = item2.w_k_b6;
        const item_w7 = item2.w_k_b7;
        const item_w8 = item2.w_k_b8;

        const zbiorcze = [item_attack,item_Defense,item_Intelligence,item_klasabroni,item_level,item_rarity];
        this.setState({
          itemsx: [...this.state.itemsx, [zbiorcze, item_rarity, item_w1, item_w2, item_w3, item_w4, item_w5, item_w6, item_w7, item_w8]]

        })
      }

      let balanceOf = await contract.methods.balanceOf(accounts[0]).call()
      for (let i = 0; i < balanceOf; i++) {
        let id = await contract.methods.tokenOfOwnerByIndex(accounts[0], i).call()
        //let tokenURI = await contract.methods.tokenURI(id).call()
        const item_currentUser = await contract.methods.items(id).call()
        console.log(item_currentUser);
        const item_attack_currentUser = item_currentUser.Attack;
        const item_Defense_currentUser = item_currentUser.Defense;
        const item_Intelligence_currentUser = item_currentUser.Intelligence;
        const item_klasabroni_currentUser = item_currentUser.klasabroni;
        const item_level_currentUser = item_currentUser.level;
        const item_rarity_currentUser = item_currentUser.rarity;
        const item_w12 = item_currentUser.w_k_b1;
        const item_w22 = item_currentUser.w_k_b2;
        const item_w32 = item_currentUser.w_k_b3;
        const item_w42 = item_currentUser.w_k_b4;
        const item_w52 = item_currentUser.w_k_b5;
        const item_w62 = item_currentUser.w_k_b6;
        const item_w72 = item_currentUser.w_k_b7;
        const item_w82 = item_currentUser.w_k_b8;
        //const zbiorcze_currentUser = "item_id: "+id+" Attack: "+item_attack_currentUser+" Defense: "+item_Defense_currentUser+" intelligence: "+item_Intelligence_currentUser+" weapon class: "+item_klasabroni_currentUser+" item level: "+item_level_currentUser+" item rarity: "+item_rarity_currentUser+ " kombinacja: "+item_w12+" "+item_w22+" "+item_w32+" "+item_w42+" "+item_w52+" "+item_w62+" "+item_w72+" "+item_w82;
        const zbiorcze_currentUser = [id,item_attack_currentUser,item_Defense_currentUser,item_Intelligence_currentUser,item_klasabroni_currentUser,item_level_currentUser,item_rarity_currentUser];
        this.setState({
          zbiorowe: [...this.state.zbiorowe, [zbiorcze_currentUser, item_rarity_currentUser,item_w12, item_w22, item_w32, item_w42, item_w52, item_w62, item_w72, item_w82]]
        })
      }

    } else {
      window.alert('Smart contract not deployed to detected network.')
    }

  
  }

  p = (xc) => {
    var node = document.getElementById(xc);
    console.log(node)
    const html = `
    <html>
      <head>
        <style>
          .[class="1"]{
            opacity: 0.3;
          }
        </style>
      </head>
      `
    htmlToImage
    .toPng(node)
    .then(function (dataUrl) {
      var img = new Image();
      img.src = dataUrl;
      document.querySelector("#tutaj").appendChild(img,{html});
    })
    .catch(function (error) {
      console.error("oops, something wents wrong!", error);
    });
  }
  TestButton = (xxx) => {
    var el = document.getElementById(xxx);
    var para = document.getElementById('statsparentid');
    // el.style.transform='scale(2)';
     el.style.width='245px';
     el.style.height='405px';
    // el.style.fontSize='2vw';
    html2canvas(
      document.getElementById(xxx)).then(canvas => 
      document.querySelector("#tutaj").appendChild(canvas)

    ) 
      // el.style.transform='scale(1)';
          console.log("adres z ktorego wysylamy ");
  }
    TestButton2 = (xxx) => {
    html2canvas(document.getElementById(xxx)).then(canvas => 
      document.body.appendChild(canvas)

    ) 
          console.log("adres z ktorego wysylamy ");
  }
  safeTransferFrom = () => {
   // 0x366d5Df19B61bB52794803af07488313AEc67DAC
    console.log("adres z ktorego wysylamy ",this.state.account)
    console.log("adres na ktory wysylamy ", this.state.address_Recipent)
    console.log("adres na ktory wysylamy ", this.address_Recipent)
    console.log("tokek id ", this.state.token_ID)
    
    this.state.contract.methods.safeTransferFrom(this.state.account, this.state.address_Recipent, this.state.token_ID).send({ from: this.state.account })
    .once('receipt', (receipt) => {
      swal("Transfer", "Successful","success");
    })
  }

  transferowe = (ether) => {
    this.state.contract.methods.transfer(this.state.address_Recipent2, this.state.token_ID2).send({ from: this.state.account })
    .once('receipt', (receipt) => {
      swal("Transfer", "Successful","success");
    })
  }

    createRandomItem = (item) => {
      this.state.contract.methods.createRandomItem().send({ from: this.state.account, value: '100000000000000000'})
      .once('receipt', (receipt) => {
        this.setState({
          items: [...this.state.items, item]
        })
      })
    }
    createRandomItem2 = (item) => {
      this.state.contract.methods.createRandomItem2().send({ from: this.state.account})
      .once('receipt', (receipt) => {
        swal("Good job!", "Your token has been minted!", "success");
        this.setState({
          items: [...this.state.items, item]
        })
      })
    }


  constructor(props) {
    super(props)
    this.state = {
      kolory:[],
      account: '',
      contract: null,
      totalSupply: 0,
      recently:[],
      itemsx: [],
      itemsx3:[],
      len:0,
      zbiorowe: [],
      token_ID: 0,
      address_Recipent: '',
      token_ID2: 0,
      adreskontraktu: '',
      address_Recipent2: '',
      tringArray:["","white", "blue", "green","orange","red"]
    }
  }
  
  render() {
    return (

      <div id="master">

        <div id="stars" style={{marginBottom:"0"}}></div>
        <div id="stars2" style={{marginBottom:"0"}}></div>
        <div id="stars3" style={{marginBottom:"0"}}></div>
  
        <div id="con">

          {/*<img id ="x" src="/x.png" alt="Logo" onClick={()=>this.TestButton2(1)}/>;*/}
          {/*<button onClick={this.p}>click me</button>*/}
          <Popup trigger={<a className="button" id="pp" > png</a>} modal>
            <div className="contentpop" id="tutaj" onClick={()=>this.p(19)}>
              <h1>Send a token</h1>
            </div>
          </Popup>
        </div>
        {/*<div className='sidebar'>
              <ProSidebar width="200px" height="400px" image="/sw.png">
                <SidebarHeader>
                  Dashboard
                </SidebarHeader>
                <Menu iconShape="square">
                  <MenuItem>Dashboard</MenuItem>
                  <SubMenu title="Components" open="true" style={{opacity:"0.8"}}>
                    <MenuItem icon={"/sw.png"}>Component 1</MenuItem>
                    <MenuItem icon="/sw.png">Component 2</MenuItem>
                  </SubMenu>
                  <SubMenu title="Reports">
                    <MenuItem>My Report</MenuItem>
                    <MenuItem>MIS Report</MenuItem>
                  </SubMenu>
                </Menu>
              </ProSidebar>
        </div>*/}
        <nav id = "arcowe" className="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
          <a className="navbar-brand col-sm-3 col-md-2 mr-0">
            <div id="title" style={{fontWeight: 300,fontSize: 20,letterSpacing: 2, marginBottom:10,marginLeft:0}}>
              <Link to="/App">
                    <span>
                      QuestOne
                    </span>
              </Link>
            </div>
          </a>
          <a className="navbar-brand col-sm-3 col-md-2 mr-0">
            <div id="title" style={{fontWeight: 300,fontSize: 20,letterSpacing: 2, marginBottom:10,marginLeft:0}}>
              <Link to="/page2">
                <span>
                  My cards
                </span>
              </Link>
            </div>
          </a>
          <a className="navbar-brand col-sm-3 col-md-2 mr-0">
            <div id="title" style={{fontWeight: 300,fontSize: 20,letterSpacing: 2, marginBottom:10,marginLeft:0}}>
              <Link to="/page1">
                <span>
                  Minted
                </span>
              </Link>
            </div>
          </a>


          <ul className="navbar-nav px-3">
            <li className="nav-item text-nowrap d-none d-sm-none d-sm-block">
              <small className="text-white"><span id="account">{this.state.account}</span></small>
            </li>
          </ul>
        </nav>
        <div id = "gora" className="container-fluid mt-5">

          <div className="row">
              <main role="main" className="col-lg-12 d-flex text-center">
                <div className="content mr-auto ml-auto">

                  <div id="title">
                    <span>
                      Issue a token
                    </span>
                    <br />
                  </div>
                  <form onSubmit={(event) => {
                    event.preventDefault()
                    this.createRandomItem2()
                    swal("Please wait", "Your weapon is beein forged", "success");
                  }}>
                    <input style={{backgroundColor:"white",color:"black"}}
                      type='submit'
                      className='btn btn-block btn-primary'
                      value='MINT'
                    />

                  </form>
                </div>
              </main>
            </div>
            <div id="pb">
              <ProgressBar transitionDuration="3s" bgColor = "green" width="75%" ariaValuemax="5000" ariaValuemin="0" completed={(this.state.totalSupply/5000)*100} />
            </div>
            <hr/>
            <div className="text-center"> <div id="title">
                    <span>
                      Currently minted
                    </span>
                    <br />
                  </div></div>

          <div className="row text-center" id="rowy">
            { this.state.itemsx.map((item, key) => {
              return(
              <div className="col-md-3 mb-3">

                <div>
                  <Tilt className = "tilt" glareEnable={true} glareMaxOpacity={0.6} perspective={500} scale={1.02} glareColor="white" glarePosition="all" style={{borderRadius: '15px'}}>
                    <div className = "card" id={key}>
                        
                        <img className="bron" style={{backgroundImage: "url(layer.png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(srodek/" + item[3] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(jelec/" + item[1] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(pommel/" + item[4] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(gora/" + item[2] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(grip/" + item[5] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(dodatki/" + item[6] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(dodatki/" + item[7] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="bron" style={{backgroundImage: "url(dodatki/" + item[8] + ".png)", backgroundPosition: '50',
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                        }}/>
                        <img className="cardpicture" src="karta.png" width="100" height="100"/>
                        <div className={item[6]}></div>
                        <div className="text" id="numer" style={{ marginLeft: "80%" ,marginTop: "50%" }}>#{key}</div>

                        

                        <div className="statsParent" id="statsparentid">
                          <div className="stats">
                            <div className="statsclass">
                              <CircularProgressbarWithChildren className="ac" maxValue="200" minValue="0" value={item[0][0]} >
                                <div >
                                  <strong>{item[0][0]}</strong>
                                </div>
                                
                              </CircularProgressbarWithChildren>
                              <div className="text" style={{ width: "100%", marginLeft: -20 ,marginTop: "-10%" }}>Attack</div>
                            </div>
                          </div>
                          <div className="stats">
                            <div className="statsclass" >
                              <CircularProgressbarWithChildren className="dc" maxValue="200" minValue="0" value={item[0][1]} >
                                <div style={{ fontSize: 12, marginTop: -5 }}>
                                  <strong>{item[0][1]}</strong>
                                </div>
                              </CircularProgressbarWithChildren>
                              <div className="text" style={{ width: "100%", marginLeft: -20 ,marginTop: "8%" }}>Defense</div>
                            </div>
                          </div>
                          <div className="stats">
                            <div className="statsclass">
                              <CircularProgressbarWithChildren  className="ic" maxValue="200" minValue="0" value={item[0][2]} >
                                <div style={{ fontSize: 12, marginTop: -5 }}>
                                  <strong>{item[0][2]}</strong>
                                </div>
                                
                              </CircularProgressbarWithChildren>
                              <div className="text" style={{ width: "100%", marginLeft: -20 ,marginTop: "26%" }}>Intelligence</div>
                            </div>
                          </div>
                          <div className="stats">
                            <div className="statsclass">
                              <CircularProgressbarWithChildren maxValue="4" minValue="0" value={item[1]} >
                                <div style={{ fontSize: 12, marginTop: -5 }}>
                                  <strong>{item[1]}</strong>
                                </div>
                                
                              </CircularProgressbarWithChildren>
                              <div className="text" style={{ width: "100%", marginLeft: -20 ,marginTop: "43%" }}>Rarity</div>
                            </div>
                          </div>
                        </div>
                    </div>
                    <div className="poswiata" style={{backgroundColor: this.state.tringArray[item[1]]}}></div>
                  </Tilt>
                </div>
              </div>
              )
            })}
          </div>
          
          
          <div>
          </div>
        </div>
        <div className="App">
             <Footer/>
         </div>
      </div>
    );
  }
}

export default Page1;
