        const item_Defense = item2.Defense;
        const item_Intelligence = item2.Intelligence;
        const item_klasabroni = item2.klasabroni;
        const item_level = item2.level;
        const item_rarity = item2.rarity;
        const item_w1 = item2.w_k_b1;
